import { Component, OnInit } from '@angular/core';
import {DataService} from '../../services/data.service';
import { GlobalsComponent} from '../globals/globals.component';
import { setScrollCookie} from '../globals/globals.component';
import { Router } from '@angular/router';

@Component({
  selector: 'app-emails',
  templateUrl: './emails.component.html',
  styleUrls: ['./emails.component.css']
})

export class EmailsComponent implements OnInit {
  postsData:any[];
  posts:Post[];
  route: Router;
  private demoRefferURL: string
  private demoUrl: string;
  private demoHeight: string;
  private demoWidth: string;
  
  constructor(private dataService:DataService,private globals: GlobalsComponent, private router: Router) { 
    this.demoRefferURL = globals.demoRefferURL;
    this.demoUrl = globals.demoUrl; 
    this.demoHeight = globals.demoHeight;
    this.demoWidth = globals.demoWidth;
    this.route = router;
  }

  ngOnInit() {
    this.dataService.getPosts("emails").subscribe((post)=>{
      console.log(post);
      this.posts=post;
      this.postsData = this.posts;
    });
  } 
 
  showDemo(thePath,theHeight,theWidth) {
    this.globals.demoRefferURL="/emails";
    this.globals.demoUrl="./assets/emails/"+thePath+"/index.html";
    this.globals.demoHeight=theHeight;
    this.globals.demoWidth=theWidth;
    this.route.navigateByUrl('/demos');
    setScrollCookie();
    
  }

  setNewPageSize() {
    alert('hello world!');
  }

}


interface Post {

}


