import { Component, OnInit } from '@angular/core';
import {DataService} from '../../services/data.service';
import { normalizeCategoryTitleDivHeights} from '../globals/globals.component';
import { deactiveNullAnchors } from '../globals/globals.component';

@Component({
  selector: 'app-videocontrol',
  templateUrl: './videocontrol.component.html',
  styleUrls: ['./videocontrol.component.css']
})

export class VideocontrolComponent implements OnInit {
  postsData:any[];
  posts:Post[];
  public normalizeCategoryTitleDivHeights = normalizeCategoryTitleDivHeights;
  public deactiveNullAnchors = deactiveNullAnchors;
  
  constructor(private dataService:DataService) { 
  }
    ngOnInit() {
    this.dataService.getPosts("video").subscribe((post)=>{
      console.log(post);
      this.posts=post;
      this.postsData = this.posts;
    });
    }
    setClickClass(theanchor: HTMLAnchorElement) {
      let hideLink:boolean = false;
      console.log(theanchor.href);
      let theLink:string = theanchor.href;
      if(theLink.indexOf("nolink")>-1) hideLink=true;
      //console.log(hideLink);
      let classes = {
        'click-through-off' : hideLink
      }
      return classes;
    }

}

interface Post {

}



